/* For mobile phones: */
*,*::before,*::after{
  margin: 0;
  padding: 0;
}

@media (max-width:768px){
    [class*="col-"] {
        width: 100%;
      }

      body{
        background-color: white;
      }
      .container{
        margin-top: 5rem;
      }
}


 a {
    text-decoration: none;
    color: black;
 } 
  
  @media (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    

    a {
        text-decoration: none !important;
        color: black;
        
     } 

     a:hover{
        text-decoration: none !important;
        color: black;
        cursor: pointer;
     }

    .card img{
        width: 100%;
        height: 24rem;
    }
      

  }