/* For mobile phones: */
@media only screen and (max-width:768px){
    [class*="col-"] {
        width: 100%;
      }

      body{
    background-color:white
      }
      .card{
        box-shadow: 0 8px 10px #f2e8e8;
      }
}


 a {
    text-decoration: none;
    color: black;
 } 
  
  @media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    

    a {
        text-decoration: none;
        color: black;
     } 

     a:hover {
      color: black;
     }

    .card-img{
        width: 100%;
        height: 18rem;
    }
    .card{
      box-shadow: 2px 8px 10px #f2e8e8;
    }
      

  }